@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans+Condensed&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{min-height:100vh;background:#fff6eb}.App{font-family:"Lato", sans-serif}.container{width:90%;max-width:850px;margin:auto}.resList{padding:1rem;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:1rem;gap:1rem}@media (max-width: 500px){.resList{grid-template-columns:1fr 1fr}}

.input{width:100%;margin:1rem 0}.input__form{display:flex;flex-wrap:wrap;justify-content:center}.input__form__item{justify-self:center}.input__form__item input{width:40vw;min-width:300px;border:none;border-bottom:2px solid #fff;height:30px;outline:0;padding-left:0.5rem}.input__form__item input:focus{border-bottom:2px solid;border-color:#ff8a00}.input__form__item input:focus::-webkit-input-placeholder{color:transparent}.input__form__item input:focus:-ms-input-placeholder{color:transparent}.input__form__item input:focus::-ms-input-placeholder{color:transparent}.input__form__item input:focus::placeholder{color:transparent}.input__form__search{background:#ffad6e;color:#333;padding:0.2rem 1rem;height:30px;cursor:pointer;border-radius:4px;font-weight:700}

.item{background:#fff;padding-bottom:0.5rem;-webkit-transition:all 200ms ease-out;transition:all 200ms ease-out;cursor:pointer;border-bottom:3px solid #fff}.item img{width:100%}.item__name{text-align:center;font-family:"Oswald", "Lato", sans-serif}.item:hover{border-bottom:3px solid #ff8a00}

.card{display:grid;grid-template-columns:0.4fr 0.6fr;padding:0.5rem;font-family:"Oswald", "Lato", sans-serif;background:#fff}.card img{width:100%}.card__content{display:flex;flex-direction:column;align-items:center}.card__name{font-size:1.6rem;color:#ff8a00}.card__cat-glass{width:100%;display:flex;justify-content:space-evenly}.card__cat-glass div{font-size:.8rem;color:#666}.card__text{width:80%;padding-top:1rem;font-family:"IBM Plex Sans Condensed", sans-serif;font-style:italic}.card__heading{width:80%;text-align:center;margin-top:1rem;color:#ff8a00;text-transform:uppercase;padding-bottom:0.3rem;font-family:"IBM Plex Sans Condensed", sans-serif}.card__ingredients{font-family:"IBM Plex Sans Condensed", sans-serif;padding-top:0rem;width:80%;display:grid;grid-template-columns:1fr 1fr 1fr}.card__ingredients div{font-size:.8rem;text-align:center;padding:0.3rem 0}@media (max-width: 500px){.card{grid-template-columns:1fr}}

.header{width:100%;background:transparent;padding:0.5rem 0;color:#333}.header .nav__hero{display:flex;align-items:center}.header .nav__hero__logo{width:40px}.header .nav__hero__logo img{width:100%}.header .nav__hero__name{margin-left:0.3rem;font-size:1.2rem;text-transform:uppercase;letter-spacing:0.05rem;font-weight:500;color:#ff8a00}

